import React from 'react';


const NavBar = ({ onClick }) => (
  <nav>
    <div className="name">
      <a href="#home">
        <h1>Evoltic</h1>
      </a>
    </div>

    <div className="nav-links">
      <a href="#advantages">
        <p> Advantages </p>
      </a>

      <a href="#projects">
        <p> Projects </p>
      </a>

      <a href="#skills">
        <p> Skills </p>
      </a>

      <a href="#contact">
        <p> Contact </p>
      </a>
    </div>

    {/* shows only on mobile devices */}

    <div className="menu-icon" onClick={onClick}>
      <i className="fas fa-bars" />
    </div>
  </nav>
)

export default NavBar;



