import React from 'react';


//shows only on Mobile phones

const Menu = ({ menuOpen, onClick, className }) => {
  if (!menuOpen) {
    return '';
  }

  return (
    <div className={`menu ${className}`}>

      <div className="menu-bar">
        <h1> Navigation </h1>
        <div onClick={onClick}>
          <i className="icon fas fa-angle-right" />
        </div>
      </div>

      <div className="menu-links">
        <a href="#home" onClick={onClick}>
          <h2> Home </h2>
        </a>

        <a href="#advantages" onClick={onClick}>
          <h2> Advantages </h2>
        </a>

        <a href="#projects" onClick={onClick}>
          <h2> Projects </h2>
        </a>

        <a href="#skills" onClick={onClick}>
          <h2> Skills </h2>
        </a>

        <a href="#contact" onClick={onClick}>
          <h2> Contact </h2>
        </a>
      </div>
    </div>
  )
}



export default Menu;



