import React from 'react';
import photo from '../img/photo.JPG'

const Contact = () => (
  <div className="contact">
    <a name="contact" className="a-link-contact" />

    <h1 className="section-name">
      Contact
    </h1>

    <div className="contact-main">

      <div className="photo-name">
        <img src={photo} alt="photo" />
        <h2> Rinat Rezakov </h2>
      </div>

      <div className="contact-icon-text">
        <i className="icon fas fa-envelope" style={{ color: "#8EC2B7" }} />
        <a href="mailto:evolticdev@gmail.com" target="_blank">
          <h2>
            evolticdev@gmail.com
          </h2>
        </a>
      </div>

      <div className="contact-icon-text">
        <i className="icon fab fa-facebook-square" style={{ color: "#3b5998" }} />
        <a
          href="https://www.facebook.com/people/Rinat-Rezakov/100006630722442"
          target="_blank"
        >
          <h2>
            Facebook
          </h2>
        </a>
      </div>

      <div className="contact-icon-text">
        <i className="icon fab fa-github-square" style={{ color: "#333" }} />
        <a href="https://github.com/Evoltic/" target="_blank">
          <h2>
            GitHub
          </h2>
        </a>
      </div>

      <div className="contact-icon-text">
        <i className="icon fas fa-map-marker-alt" style={{ color: "#E53416" }} />
        <h2>
          Kazan, Russia
        </h2>
      </div>

      <h2>
        Free to relocate 
      </h2>

    </div>
  </div>
)


export default Contact;



