import React from 'react';


const SkillBar = ({ className, name }) => (
  <div className={`skill-bar ${className}`}>

    <p>{ name }</p>

    <div className="skill-bar-background">
      <div className="skill-bar-progress" />
    </div>

  </div>
)


export default SkillBar;



