import React from 'react';

import SkillBar from './SkillBar.js';
import ScewBackground from './ScewBackground.js'


const Skills = () => (
  <div className="skills">
    <ScewBackground>
      <div className="skills-content">

        <h1 className="section-name">
          Skills
        </h1>

        <a name="skills" className="a-link-skills" />

        <div className="skills-main">

          <div className="tech-stack">

            <h2> Technology stack: </h2>

            <ul>
              <li>
                <p>
                  <b> HTML </b>
                 </p>
              </li>

              <li>
                <p>
                  <b> CSS, Sass </b>
                </p>
              </li>

              <li>
                <p>
                  <b> JavaScript </b>
                  (ES6 / ES7 / ES8)
                </p>
              </li>

              <li>
                <p>
                  Libraries:
                  <b> React, Redux, React Router </b>
                </p>
              </li>

              <li>
                <p>
                  Tools:
                   <b> npm, create-react-app, Git </b>
                 </p>
              </li>
            </ul>

          </div>

          <div className="skill-bars">

            <SkillBar
              className="learning-bar"
              name="Learning speed"
            />

            <SkillBar
              className="clean-code-bar"
              name="Writing clean code"
            />

            <SkillBar
              className="love-bar"
              name="Love in projects"
            />
          </div>
        </div>
      </div>
    </ScewBackground>
  </div>
)


export default Skills;



