import React from 'react';

import Project from './Project.js';
import SmallProject from './SmallProject.js'

import ssLc from '../img/lc.png';
import ssGol from '../img/gol.png';
import ssAlike from '../img/alike.png';


const Projects = () => (
  <div className="projects">
    <a name="projects" className="a-link-projects" />

    <h1 className="section-name">
      Projects
    </h1>

    <div className="big-projects">
      <Project
        className="project project-twinz"
        head="A like movie"
        main={`Simple and powerful search system for similar movies.
          Just select a movie and get a list of films, which closest to it by a spirit.
          `}
        link="alikemovie"
        tech="React"
        img={ssAlike}
      />

      <Project
        className="project project-lc"
        link="create-level"
        head="Level Creator"
        main={`Create simple game levels.
              Helpful for developers who wants to create levels in a 2D game\
              in the simplest way.
              No need to download. Just open and draw.`}
        tech="React + Redux + Router"
        img={ssLc}
      />

      <Project
        className="project project-gl"
        head="The Game of Life"
        main={`Playfield is a collection of cells which, \
          based on a few mathematical rules, can live, die or multiply.
          You can give life to a cell or take it back in real time.
          The game was invented by mathematician John Conway.
          `}
        link="the-game-of-life"
        tech="React"
        img={ssGol}
      />
    </div>

    <div className="sm-projects">
      {/* <SmallProject
        head="Recipe Box"
        main="Create recipes that have names and ingredients"
        tech="React"
        link="https://evoltic.github.io/recipe-box/"
      /> */}

      <SmallProject
        head="FCC Leaderboard"
        main="The top 100 freeCodeCamp campers"
        tech="React"
        link="https://evoltic.github.io/camper-leaderboard/"
      />

      <SmallProject
        head="Wiki Viewer"
        main="Search Wikipedia entries in a search box and
         see the resulting Wikipedia entries"
        tech="jQuery"
        link="https://codepen.io/Evoltic/full/ZadJre/"
      />

      <SmallProject
        head="Twitch Cheker"
        main="See whether picked streamers are currently streaming on Twitch.tv."
        tech="jQuery"
        link="https://codepen.io/Evoltic/full/ZvbazQ/"
      />
    </div>
  </div>
)


export default Projects;



