import React from 'react';

import ScewBackground from './ScewBackground.js'


const Advantages = () => (
  <div className="advantages">

    <ScewBackground>
      <div className="advantages-content">

        <h1 className="section-name">
          Advantages
        </h1>

        <a name="advantages" className="a-link-advantages" />
        
        <ul className="advantages-main">
          <li>
            <h3> Balance between User Experience and design </h3>
          </li>

          <li>
            <h3> Maximum performance and Best practices </h3>
          </li>

          <li>
            <h3> Unique design </h3>
          </li>

          <li>
            <h3> Responsive design </h3>
          </li>

          <li>
            <h3> Reusable, clean and understandable code </h3>
          </li>
        </ul>

      </div>
    </ScewBackground>
  </div>
)

export default Advantages;



