import React from 'react';


const SmallProject = ({ className, head, main, tech, link }) => (
  <div className="sm-project">
    <a href={ link } target="_blank">
      <div className="sm-project-text">
        <h1 className="sm-project-head">{ head }</h1>
        <p className="sm-project-main">{ main }</p>
        <p className="sm-project-tech">{ tech }</p>
      </div>
    </a>
  </div>
)


export default SmallProject;



