import React from 'react';


const style = {
  transform: 'skewY(2deg)',
  backgroundColor: '#0575e6',
  boxShadow: 'inset 0px 2px 4px 0 #23282d, inset 0px -1px 1px 0 #23282d',
  filter: 'blur(0.3px)',
  padding: '1.2%'
}


const ScewBackground = (props) => (
  <div style={{ position: 'relative' }}>

    <div style={style}>
      <div style={{ opacity: '0' }}>
        {props.children}
      </div>
    </div>

    <div style={{ position: 'absolute', top: '0' }}>
      {props.children}
    </div>

  </div>
)


export default ScewBackground;



